li{
    list-style: none !important;
}
a {
    background-color: goldenrod;
    text-decoration: none;
    color: white;
    border-radius: .25rem;
    text-align: center;
    display: inline-block;
    transition: all .3s;
    padding: 1rem;
}
  
a:hover {
    opacity: .6;
}

.breweries {
    padding: 2rem;
}
  
.breweries > ul {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    grid-gap: 1rem;
}
  
.breweries > ul > li {
    border: 1px solid #E2E2E2;
    border-radius: .5rem;
}
  
.breweries > ul > li > figure {
    max-height: 220px;
    overflow: hidden;
    border-top-left-radius: .5rem;
    border-top-right-radius: .5rem;
    position: relative;
}
.breweries > ul > li > figure > img {
    width: 100%;
}
  
.breweries > ul > li > figure > figcaption {
    position: absolute;
    bottom: 0;
    background-color: rgba(0,0,0,.7);
    width: 100%;
}
  
.breweries > ul > li > figure > figcaption > h3 {
    color: white;
    padding: .75rem;
    font-size: 1.25rem;
}
  
.breweries > ul > li > p {
    font-size: 1rem;
    line-height: 1.5;
    padding: 1rem .75rem;
    color: #666666;
}
  
.breweries > ul > li > a {
    padding: .5rem 1rem;
    margin: .5rem;
}